.highlight {
    font-size: larger;
    font-weight: bold;
    color: var(--text-secondary);
    filter: drop-shadow(0px 0px 0px gray);
    transition: all 100ms ease-in;
}

.highlight:hover {
    filter: drop-shadow(0px 0px 10px gray);
    color: var(--accent-primary);
}

#home {
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    h1 {
        text-align: center;
        width: auto;
    }
    img {
	display: relative;
	padding: 1rem;
	border-radius: 50%;
        width: 40%;
        max-width: 20rem;
	box-shadow: 1rem 1rem 1rem rgba(255,255,255,1),
		-1rem -1rem 1ren rgba(0,0,0,1);
    }
}

/* Small screens */
@media only screen and (max-width: 599px) {
    #home{
        height: 90vh;
    }
    
}
  
