
.navbar {
    position: fixed;
    background-color: var(--bg-primary);
    transition: width 200ms ease;
  z-index: 1;
  }
  
  .navbar-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
  .nav-item {
    width: 100%;
    display:flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    filter: grayscale(100%) opacity(0.7);
    transition: var(--transition-speed);
  }
  
  .nav-item:last-child {
    margin-top: auto;
  }
  
  .nav-item:hover {
    filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
  }
  
  .link-text {
    // display: none;
    // visibility: hidden;
  }
  
  .nav-item svg {
    min-width: 2rem;
    max-width: 2rem;
    margin: 0 1.5rem;
  }
  
  .fa-primary {
    color: var(--accent-primary);
  }
  
  .fa-secondary {
    color: var(--accent-secondary);
  }
  
  .fa-primary, .fa-secondary {
    transition: var(--transition-speed);
  }
  
  .logo {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1rem;
    text-align: center;
    color: var(--text-secondary);
    background: var(--bg-secondary);
    font-size: 1.5rem;
    letter-spacing: 0.3ch;
    width: 100%;
    justify-content: space-between;
  }
  
  .logo svg {
    transform:  rotate(0deg);
    transition: transform var(--transition-speed);
  
  }
  
  .navbar:hover .logo svg {
    transform: rotate(-180deg);
  }
  
  /* Small screens */
  @media only screen and (max-width: 599px) {
    .navbar {
      bottom: 0;
      width: 100vw;
      height: 5rem;
    }
  
    .logo {
      display: none;
    }
  
    .navbar-nav {
      flex-direction: row;
    }
  
    .nav-link {
      justify-content: center;
    }

    .link-text {
      display: none;
    }
  
    .nav-item {
     width: 20vw;
     justify-content: center;
    }
  
    .nav-item svg {
      // min-width: 0;
      margin: 0 5vw;
    }
  
    main {
      margin: 0;
    }
  }
  
  /* Large screens */
  @media only screen and (min-width: 600px) {
    .navbar {
      top: 0;
      width: 5rem;
      height: 100vh;
    }
  
    .navbar:hover {
      width: 16rem
    }

    .navbar .link-text{
      visibility: hidden;
      max-width: 0;
      overflow: hidden;
    }
  
    .navbar:hover .link-text {
      visibility: visible;
      max-width: 100vh;
      margin-left: 1rem;
      transition: all var(--transition-speed) ease;
    }
  }
  
  .theme-icon {
    display: none;
  }
  
  .dark #darkIcon {
    display: block;
  }
  
  .light #lightIcon {
    display: block;
  }
  
  .solar #solarIcon {
    display: block;
  }